import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListPxpData from "../../../_pxp/components/ListPxp/ListPxpData";
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import moment from 'moment';
import Icon from '@material-ui/core/Icon';
import { getUrlForView, capitalizeFirst } from '../../../_pxp/utils/Common';
import BasicFilters from '../../../_pxp/components/filters/BasicFilters';
import { useHistory } from 'react-router-dom';

const ExampleList = () => {
  
  const history = useHistory();
  
  const useStyles = makeStyles((theme) => ({
    root: {
      // width: '100%',
      // maxWidth: '100%',
      // backgroundColor: theme.palette.background.paper,
    },
    inline: {
      display: 'inline',
    },
  }));

  const options = [
    {
      field: 'estado',
      value: 'pendientes',
      icon: 'alarm_on',
      label: 'Pendientes',
    },
    {
      field: 'estado',
      value: 'finalizadas',
      icon: 'alarm_on',
      label: 'Finalizadas',
    },
    {
      field: 'estado',
      value: 'todas',
      icon: 'done_all',
      label: 'todas',
    },
    {
      field: 'usucli.desc_persona#usudama.alias',
      search: true,
    },
  ];
  
  const goTo = (url, id) => history.push("/citas/detail/" + id);

  const getAvatarAlt = (name) => {
    if (name.includes(" ")) {
      return name.split(" ")[0].charAt(0) + name.split(" ")[1].charAt(0)
    } else {
      return name.charAt(0)
    }
  };

  const classes = useStyles();

  const listConfig = {
    showActions: false,
    showDetail: false,
    showSearch: false,
    showFilter: true,
    filters: options,
    columns: {
      render: (row) => {
        return <ListItem button className={classes.root}
          onClick={(event) => goTo(event, row.id_cita)}>
          <ListItemAvatar>
            <Avatar
              alt={getAvatarAlt(row.nombre)}
              src={
                row.cliente_dama === 'cliente'
                  ? getUrlForView({
                    nameFile: row.nombre_archivo,
                    folder: row.folder,
                    extension: row.extension,
                  })
                  : row.url_foto_cliente
              }
            />
          </ListItemAvatar>
          <ListItemText id={row.id_cita} primary={row.nombre} secondary={
            <React.Fragment>
              <Typography
                component="span"
                variant="body2"
                color="textPrimary"
              >
                {moment(row.fecha).format('DD/MMM/YYYY')} {moment(row.hora, "HH:mm:ss").format("hh:mm a")}
                <br />
                <strong>{capitalizeFirst(row.estado_cita)}</strong>
              </Typography>

            </React.Fragment>
          } />

          <ListItemSecondaryAction>
            <Icon color="secondary">chevron_right</Icon>
          </ListItemSecondaryAction>
        </ListItem>;
      }
    },
    getDataTable: {
      url: 'citas/Cita/listarCita',
      params: {
        start: 0,
        sort: 'cit.fecha_ultimo_estado',
        dir: 'desc',
        //limit: 10
      },
    },
  };

  return (
    <div style={{ height: 'calc(var(--vh) - 56px)', display: 'flex', flexGrow: 1, position: 'relative' }}>
      <ListPxpData config={listConfig} FilterComponent={BasicFilters} heightFilter={48} />
    </div>
  )
};

export default ExampleList;
